@keyframes riseToTop {
  from {
    transform: translateY(31px); }
  to {
    transform: translateY(0); } }

* {
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  outline: none; }

body {
  margin: 0;
  font-size: 16px; }

a {
  text-decoration: none;
  color: black; }
  @media screen and (hover: hover) {
    a:hover {
      text-decoration: underline; } }

.o-v {
  overflow: visible !important; }

.d-none {
  display: none !important; }

button {
  border: none;
  border-radius: 10px;
  padding: 5px 15px; }
  button.primary {
    color: purple;
    background: #fff; }
  button.flat {
    background: none; }
    button.flat.light {
      color: #fff; }
    button.flat.dark {
      color: #1d1d1d; }

