  font-weight: normal;
  direction: rtl; }

*[dir=ltr] {
  direction: ltr; }

#root {
  background: #e8e8e8;
  width: 100vw;
  height: 100vh;
  overflow-x: hidden;
  overflow-y: auto;
  color: #3d3d3d; }
  #root * {
    box-sizing: border-box; }

.container {
  padding: 1rem 2rem 3rem 2rem; }
